.dashboard {
    margin: auto;
    text-align: center;
    font-family: "open sans", Arial, Helvetica, sans-serif;
    font-size: 12px;
}

.page-layout {
    padding: 10px;
}
.page-title {
    text-align: center;
    padding: 15px 10px;
    font-family: 'Gruppo', sans-serif;
    font-size: 18pt;
    font-weight: bold;
    color: white;
    text-transform: uppercase;
    background: url(https://cdn.pixabay.com/photo/2020/02/15/14/33/network-4851119_1280.jpg);
}

.page-footer {
    padding:20px;
    border-top: 0.5px solid silver;
    font-size: 10px;
    line-height: 2em;
}

.chart-header {
    padding-bottom: 20px;
    font-size: 12pt;
    text-transform: uppercase;
    margin: 0;
}


.site-link {
    cursor: pointer;
    color: blue;
}

.page-footer {
    background-color: black;
    color: #cacaca;
}

.footer-site-link {
    cursor: pointer;
    color: lightgreen;
}

/*
This style is used by several components contained in the dashboard component. 
*/
.dashboard-component {
    width: 97%;
    max-width: 800px;
    text-align: center;
    line-height: 2em;
    padding: 5px;
    border: 0.5px solid silver;
    border-radius: 10px;
    margin: auto;
    margin-bottom: 10px;
}




  